@import '~assets/styles/h3Imports';

.medical-schedule-card {
  align-items: center;
  background-color: var(--white);
  border-radius: 8px;
  box-shadow: var(--box-shadow);
  display: flex;
  height: 100%;
  padding: 0 rem(16);

  --button-min-width: 160px;
}

.medical-schedule-card[data-type='medication-taken'] {
  border-left: solid 8px var(--medical-schedule-card-taken);
}

.medical-schedule-card[data-type='medication-missed'] {
  border-left: solid 8px var(--medical-schedule-card-missed);
}

.medical-schedule-card[data-type='medication-skipped'] {
  border-left: solid 8px var(--medical-schedule-card-skip);
}

.medical-schedule-card[data-type='medication-comming-up'] {
  border-left: solid 8px var(--medical-schedule-card-comingup);
}

.medical-schedule-card-mobile {
  display: none;
}

.button-wraper {
  align-items: center;
  display: flex;
  flex: 1 1 auto;
  justify-content: right;
}

.buttons {
  padding-right: rem(24);

  a,
  button {
    font-size: rem(14);
    margin-bottom: 0;
  }
}

.icons {
  padding-right: rem(16);

  span {
    height: rem(48);
    width: rem(48);
  }
}

.timer {
  @include h2;

  padding-right: rem(82);
}

.counter {
  @include h5;

  padding-right: rem(87);
}

.counter-time {
  color: var(--gray-3);
  padding-left: rem(4);
}

.status-tablet {
  display: none;
}

.status,
.status-tablet {
  @include h5;

  padding-right: rem(75);
  width: rem(175);

  [data-type='medication-taken'] & {
    color: var(--medical-schedule-card-taken);
  }

  [data-type='medication-missed'] & {
    color: var(--medical-schedule-card-missed);
  }

  [data-type='medication-skipped'] & {
    color: var(--medical-schedule-card-skip);
  }

  [data-type='medication-comming-up'] & {
    color: var(--medical-schedule-card-comingup);
  }
}

@media #{$tablet-down} {
  .status {
    display: none;
  }

  .timer {
    min-width: rem(136);
    padding-right: rem(30);
  }

  .counter {
    padding-right: rem(41);
  }

  .status-tablet {
    display: block;
  }
}

@media #{$small-down} {
  .medical-schedule-card {
    display: none;
  }

  .medical-schedule-card-mobile {
    background-color: var(--white);
    border-radius: 8px;
    box-shadow: var(--box-shadow);
    display: block;
  }

  .container {
    padding: 0 rem(16) rem(26);
  }

  .status-mobile {
    @include h4;

    align-items: center;
    border-radius: 8px 8px 0 0;
    color: var(--white);
    display: flex;
    height: rem(40);
    justify-content: center;
    margin-bottom: rem(16);

    [data-type='medication-taken'] & {
      background-color: var(--medical-schedule-card-taken);
    }

    [data-type='medication-missed'] & {
      background-color: var(--medical-schedule-card-missed);
    }

    [data-type='medication-skipped'] & {
      background-color: var(--medical-schedule-card-skip);
    }

    [data-type='medication-comming-up'] & {
      background-color: var(--medical-schedule-card-comingup);
    }
  }

  .icons-mobile {
    @include h2;

    align-items: center;
    display: flex;

    span {
      padding-right: rem(20);
    }
  }

  .counter-mobile {
    @include h5;
  }

  .counter-time-mobile {
    color: var(--gray-3);
    padding-left: rem(4);
  }

  .divider {
    background-color: var(--brand-secondary);
    height: rem(1);
    margin: rem(16) 0;
    opacity: 0.16;
  }

  .buttons-mobile {
    display: flex;
    flex-direction: column;

    a,
    button {
      margin-bottom: 0;
    }
  }

  .counter-mobile-wraper {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
}
